body {
  background-color: pink;
}

h1 {
  text-align: center;
  color: #fff;
  font-family: Courier New, Courier, monospace;
  font-size: 60px;
}

#postsContainer {
  grid-template-rows: minmax(500px, 612px);
  grid-template-columns: repeat(auto-fill, 470px);
  gap: 45px;
  display: grid;
}

#createPostForm, .post {
  background-color: #fff;
  border: 2px solid #000;
  flex-direction: column;
  padding: 20px 0;
  display: flex;
}

.form-cont, .post {
  box-shadow: 0 0 8px #000;
}

#createPostForm {
  border: none;
}

.form-cont {
  border: 2px solid #000;
}

#titleInput, #contentInput {
  border: 2px solid #000;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  transition: all .25s linear;
}

#titleInput {
  width: 80%;
  font-size: 20px;
}

#contentInput {
  width: 90%;
  height: 80%;
  min-height: 450px;
  resize: none;
}

#createPostForm > button {
  width: 40%;
  cursor: pointer;
  border: none;
  margin-left: auto;
  margin-right: auto;
  padding: 10px;
  font-size: 20px;
  transition: all .25s linear;
}

#contentInput:focus, #titleInput:focus, #createPostForm > button:hover, #createPostForm > button:focus {
  outline: none;
  box-shadow: 0 0 8px rgba(0, 0, 0, .5);
}

.post {
  text-align: center;
}

.post > h2, .post > p {
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
}

.post > p {
  width: 90%;
  max-height: 400px;
  border: 2px solid #000;
  padding: 10px;
  overflow: auto;
}

.post-param-btns {
  justify-content: space-evenly;
  display: flex;
}

.editPostButton, .deletePostButton, .commentsPostButton {
  box-sizing: content-box;
  width: 32px;
  height: 32px;
  cursor: pointer;
  border: none;
  margin-top: 10px;
  padding: 10px;
  transition: all .25s linear .25s;
}

.commentsPostButton {
  width: 100px;
  font-size: 20px;
}

.svg-btn {
  width: 100%;
  height: 100%;
  transition: all .25s linear .25s;
}

.editPostButton:hover, .deletePostButton:hover {
  rotate: 225deg;
  box-shadow: 0 0 8px #000;
}

.editPostButton:hover > svg, .deletePostButton:hover > svg {
  rotate: -225deg;
}

.commentsPostButton:hover {
  box-shadow: 0 0 8px #000;
}

.commentsContainer {
  height: 100%;
  position: relative;
}

.comments-list {
  text-align: left;
  height: 80%;
  border-top: 2px solid #000;
  margin: 0;
  padding-top: 10px;
  padding-bottom: 10px;
  list-style: none;
  overflow-y: scroll;
}

.comment-item {
  width: max-content;
  background-color: #d2d0d0;
  border-radius: 35px;
  padding: 5px 15px;
  font-size: 20px;
}

.comment-item:not(:last-of-type) {
  margin-bottom: 5px;
}

.createCommentForm {
  width: 100%;
  height: 50px;
  border-top: 2px solid #000;
  padding: 0;
  display: flex;
  position: absolute;
  bottom: -20px;
}

.createCommentForm > input, .createCommentForm > button {
  height: 100%;
  border: none;
  padding: 0;
}

.createCommentForm > input {
  width: 85%;
  box-sizing: border-box;
  outline: none;
  padding-left: 10px;
  font-size: 20px;
}

.createCommentForm > button {
  width: 15%;
  cursor: pointer;
  border-left: 2px solid #000;
}

.send-comment__svg {
  width: 100%;
  height: 100%;
  justify-content: center;
  display: flex;
}

.exit-comments-btn {
  width: 45px;
  height: 45px;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 50%;
  padding: 0;
  transition: all .25s linear;
  position: absolute;
  top: -10px;
  right: 10px;
}

.exit-comments-btn:hover {
  rotate: 270deg;
  box-shadow: 0 0 8px #000;
}

.exit-btn__svg {
  width: 100%;
  height: 100%;
}

